"use client";

import React, {
  createContext,
  useContext,
  useEffect,
  useState,
  useCallback,
  useMemo,
} from "react";

type DecodedToken = {
  id?: string;
  email?: string;
  firstName?: string;
  lastName?: string;
  fullName?: string;
  userGroupId?: string;        // "1" ou "2"
  isAdmin?: string | boolean;  // "true"/"false" ou boolean
  authProvider?: string;       // Local ou Google
  phone?: string;
  [key: string]: unknown;
};

type LoginResult = { success: true } | { success: false; error?: string };
type RegisterResult = { success: true } | { success: false; error?: string };

type AuthContextValue = {
  token: string | null;
  user: DecodedToken | null;
  isAdmin: boolean;
  login: (identifier: string, password: string) => Promise<LoginResult>;
  register: (input: {
    firstName: string;
    lastName: string;
    email: string;
    password: string;
    confirmPassword: string;
    phone?: string;
  }) => Promise<RegisterResult>;
  loginWithToken: (jwt: string) => void; // usado no Google
  logout: () => void;
  updateUserFromProfile: (data: {
    firstName?: string;
    lastName?: string;
    email?: string;
    phone?: string;
  }) => void;
  isLoading: boolean;
};

const AuthContext = createContext<AuthContextValue | undefined>(undefined);

const API_BASE_URL =
  process.env.NEXT_PUBLIC_API_BASE_URL ?? "https://localhost:7105";

/* ======================================================
   Decodificação do JWT (com correção de padding)
====================================================== */
function decodeJwt(token: string): DecodedToken | null {
  try {
    const payload = token.split(".")[1];
    if (!payload) return null;

    let base64 = payload.replace(/-/g, "+").replace(/_/g, "/");

    // padding faltante
    const pad = base64.length % 4;
    if (pad === 2) base64 += "==";
    else if (pad === 3) base64 += "=";

    const json = atob(base64);
    return JSON.parse(json);
  } catch {
    return null;
  }
}

/* ======================================================
   Normalização do usuário (fora do componente)
====================================================== */
function normalizeUser(decoded: DecodedToken): DecodedToken {
  const provider =
    decoded.authProvider ??
    (decoded as any).AuthProvider ??
    (decoded as any).provider ??
    "Local";

  const normalizedProvider =
    typeof provider === "string" ? provider.trim() : "Local";

  const firstName = decoded.firstName as string | undefined;
  const lastName = decoded.lastName as string | undefined;

  let fullName = decoded.fullName as string | undefined;
  if (!fullName && firstName) {
    fullName = lastName ? `${firstName} ${lastName}` : firstName;
  }

  return {
    ...decoded,
    authProvider: normalizedProvider || "Local",
    fullName,
  };
}

/* ======================================================
   AuthProvider
====================================================== */
export const AuthProvider: React.FC<{ children: React.ReactNode }> = ({
  children,
}) => {
  const [token, setToken] = useState<string | null>(null);
  const [user, setUser] = useState<DecodedToken | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  // Carrega token do localStorage no primeiro render
  useEffect(() => {
    if (typeof window === "undefined") return;

    const stored = window.localStorage.getItem("memoriza_token");
    if (stored) {
      const decoded = decodeJwt(stored);
      if (decoded) {
        const normalized = normalizeUser(decoded);
        setToken(stored);
        setUser(normalized);
      }
    }
    setIsLoading(false);
  }, []);

  /* ======================================================
     Aplica token + salva no localStorage + decodifica
     (memoizado para não mudar a referência a cada render)
  ====================================================== */
  const applyToken = useCallback((jwt: string) => {
    if (typeof window !== "undefined") {
      window.localStorage.setItem("memoriza_token", jwt);
    }

    const decoded = decodeJwt(jwt);
    if (decoded) {
      const normalized = normalizeUser(decoded);
      setUser(normalized);
    } else {
      setUser(null);
    }

    setToken(jwt);
  }, []);

  // Usado no callback do Google
  const loginWithToken = useCallback(
    (jwt: string) => {
      applyToken(jwt);
    },
    [applyToken]
  );

  /* ======================================================
     LOGIN NORMAL (memoizado)
  ====================================================== */
  const login = useCallback(
    async (identifier: string, password: string): Promise<LoginResult> => {
      try {
        const response = await fetch(`${API_BASE_URL}/api/Auth/login`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ identifier, password }),
        });

        const data = await response.json();
        if (!response.ok || !data?.token) {
          return {
            success: false,
            error:
              data?.message ||
              "Não foi possível fazer login. Tente novamente.",
          };
        }

        applyToken(data.token);
        return { success: true };
      } catch (error) {
        console.error("Erro ao fazer login:", error);
        return {
          success: false,
          error:
            "Erro ao conectar ao servidor. Verifique sua conexão e tente novamente.",
        };
      }
    },
    [applyToken]
  );

  /* ======================================================
     REGISTER NORMAL (memoizado)
  ====================================================== */
  const register = useCallback(
    async (input: {
      firstName: string;
      lastName: string;
      email: string;
      password: string;
      confirmPassword: string;
      phone?: string;
    }): Promise<RegisterResult> => {
      try {
        const response = await fetch(`${API_BASE_URL}/api/Auth/register`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify(input),
        });

        const data = await response.json();
        if (!response.ok || !data?.token) {
          return {
            success: false,
            error:
              data?.message ||
              "Não foi possível criar a conta. Verifique os dados e tente novamente.",
          };
        }

        applyToken(data.token);
        return { success: true };
      } catch (error) {
        console.error("Erro ao registrar usuário:", error);
        return {
          success: false,
          error:
            "Erro ao conectar ao servidor. Verifique sua conexão e tente novamente.",
        };
      }
    },
    [applyToken]
  );

  /* ======================================================
     LOGOUT (memoizado)
  ====================================================== */
  const logout = useCallback(() => {
    setToken(null);
    setUser(null);
    if (typeof window !== "undefined") {
      window.localStorage.removeItem("memoriza_token");
    }
  }, []);

  /* ======================================================
     Atualiza dados do usuário vindos da página de perfil
  ====================================================== */
  const updateUserFromProfile = useCallback(
    (data: { firstName?: string; lastName?: string; email?: string; phone?: string }) => {
      setUser((prev) => {
        if (!prev) return prev;

        const next: DecodedToken = {
          ...prev,
          ...data,
        };

        const fn = (data.firstName ?? prev.firstName) as string | undefined;
        const ln = (data.lastName ?? prev.lastName) as string | undefined;

        if (fn) {
          next.fullName = ln ? `${fn} ${ln}` : fn;
        }

        return next;
      });
    },
    []
  );

  /* ======================================================
     ADMIN CHECK (1 = comum, 2 = admin)
  ====================================================== */
  const isAdmin =
    !!user &&
    (user?.isAdmin === true ||
      user?.isAdmin === "true" ||
      user?.userGroupId === "2");

  const value = useMemo<AuthContextValue>(
    () => ({
      token,
      user,
      isAdmin,
      isLoading,
      login,
      register,
      loginWithToken,
      logout,
      updateUserFromProfile,
    }),
    [token, user, isAdmin, isLoading, login, register, loginWithToken, logout, updateUserFromProfile]
  );

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};

// Hook
export const useAuth = (): AuthContextValue => {
  const ctx = useContext(AuthContext);
  if (!ctx) {
    throw new Error("useAuth deve ser utilizado dentro de um AuthProvider");
  }
  return ctx;
};